@import url(https://fonts.googleapis.com/css?family=Titillium+Web);

body {
    font-family: 'Titillium Web', sans-serif;
    margin: 0 1rem;
    font-size: 16px;
}

h1 {
    font-size: 6rem;
    line-height: 1;
    margin: 1rem 0;
    font-weight: 900;
}
.cups {
	-webkit-animation: beat 2s ease infinite alternate;
	        animation: beat 2s ease infinite alternate;
}
@-webkit-keyframes beat {
	from {	-webkit-transform: rotate(-20deg);	transform: rotate(-20deg); }
	to {	-webkit-transform: rotate(20deg);	transform: rotate(20deg); }
}
@keyframes beat {
	from {	-webkit-transform: rotate(-20deg);	transform: rotate(-20deg); }
	to {	-webkit-transform: rotate(20deg);	transform: rotate(20deg); }
}
