@import url('https://fonts.googleapis.com/css?family=Titillium+Web');

body {
    font-family: 'Titillium Web', sans-serif;
    margin: 0 1rem;
    font-size: 16px;
}

h1 {
    font-size: 6rem;
    line-height: 1;
    margin: 1rem 0;
    font-weight: 900;
}
.cups {
	animation: beat 2s ease infinite alternate;
}
@keyframes beat {
	from {	transform: rotate(-20deg); }
	to {	transform: rotate(20deg); }
}